.alert-danger , .alert-success{
    padding: 10px 10px !important;
    p{

        display: flex;
    align-items: center;
        font-size: 15px;
        i{
            margin: 0 8px;
        }
    }

    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 0px !important;
}


.alert-danger {
    p{
        color:#ff6b61 !important;
    }
}

